.search {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;

    .box {
        transform: translateX(100%);
        transition: all .3s;
        background-color: #f3f3f4;
        height: 100%;
        padding-top: 36px;
        overflow: auto;

        .content {
            padding: 0 10px;

            .singer {
                .title {
                    line-height: 30px;
                    font-size: 12px;
                }

                .am-list-item {
                    background-color: #f3f3f4;
                    border-bottom: 1px solid #e9e9e9;
                    padding: 5px 0;

                    .am-list-thumb {
                        img {
                            width: 50px;
                            height: 50px;
                            margin-right: 10px;
                            border-radius: 5px;
                        }
                    }
                }
                .am-list-content {
                    font-size: 14px
                }
            }

            .songMenuList {
                .title {
                    line-height: 30px;
                    font-size: 12px;
                }

                .am-list-body::before {
                    display: none;
                }
                .am-list-item {
                    background-color: #f3f3f4;
                    border-bottom: 1px solid #e9e9e9;
                    padding: 5px 0;

                    .am-list-line {
                        &::after {
                            display: none;
                        }
                    }

                    .am-list-thumb {
                        img {
                            width: 50px;
                            height: 50px;
                            margin-right: 10px;
                            border-radius: 5px;
                        }
                    }


                }

                .am-list-content {
                    font-size: 14px
                }
            }

            .songList {
                margin-top: 5px;

                .am-list-body::before{
                    display: none;
                }
                .am-list-item {
                    background-color: #f3f3f4;
                    .am-list-content {
                        padding: 2px 0;
                    }
                }
                .am-list-brief {
                    font-size: 12px;
                }
                .am-list-content {
                    font-size: 15px
                }
                .am-list-item {
                    padding: 0;
                }
            }
        }

        .hotKey {
            padding-left: 17px;
            .title {
                color: #c09dac;
                line-height: 50px;
                margin-top: 15px;
            }
            .content {
                padding: 0;
                .am-tag {
                    height: 22px;
                    line-height: 22px;
                    color: #000;
                    padding: 0 11px;
                    margin-right: 28px;
                    margin-bottom: 15px;
                    &::before{
                        border: none;
                    }
                }
            }
        }
    }

    .searchBar {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        display: flex;
        background-color: #d44438;
        padding: 8px;
        color: #fff;
        align-items: center;
        transform: translateX(100%);

        .icon-xiangzuojiantou {
            margin-right: 8px;
            font-size: 14px
        }

        input {
            flex: 1;
            height: 20px;
            background-color: #d44439;
            border-bottom: 1px solid #e4e5e4;
            color: #fff;
            font-size: 14px;

            &::placeholder {
                color: #fff;
            }
        }

        .icon-guanbi {
            font-size: 14px;
            margin-left: 8px;
        }
    }
}